.filter-item {
  height: 0;
  width: 0;
}

.single-project-item {
  position: relative;
  overflow: hidden;
  display: block;
  margin-bottom: 30px;
  /*        box-shadow: 0 20px 50px rgba(59, 43, 91, 0.7);
     */
}

.show {
  height: 250px;
  width: 250px;
  margin-left: 10px;
}

.project-container {
  display: flex;
  width: 100%;
  flex: inherit;
  flex-flow: wrap;
}
.img-holder {
  display: block;
  overflow: hidden;
  position: relative;
}

.img-holder img {
  transform: scale(1.1, 1.1);
  transition: all 0.5s ease 0s;
  width: 100%;
  height: 242px;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s linear;
}

.overlay-style-one {
  position: absolute;
  background: rgba(0, 0, 0, 0.6);
  height: 100%;
  left: 0;
  top: 0;
  width: 100%;
  opacity: 0;
  text-align: center;
  -webkit-transform: perspective(400px) rotateX(-90deg);
  -moz-transform: perspective(400px) rotateX(-90deg);
  -ms-transform: perspective(400px) rotateX(-90deg);
  -o-transform: perspective(400px) rotateX(-90deg);
  transform: perspective(400px) rotateX(-90deg);
  -webkit-transform-origin: top;
  -moz-transform-origin: top;
  -ms-transform-origin: top;
  -o-transform-origin: top;
  transform-origin: top;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s;
}

.overlay-style-one .box {
  display: table;
  height: 100%;
  width: 100%;
}

.overlay-style-one .box .content {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.overlay-style-one .box .content button {
  opacity: 1;
  border: 1px solid #fdc900;
  background: #fdc900;
  display: inline-block;
  border-radius: 3px;
  font-size: 16px;
  color: #222222;
  font-weight: 400;
  transition: all 500ms ease;
  padding: 4px 15px 4px;
  text-decoration: none;
}

.box .content .text-holder {
  position: absolute;
  bottom: 10px;
  left: 20px;
  text-align: left;
}

.box .content .text-holder h4 {
  color: #ffffff;
  font-size: 18px;
  font-weight: 400;
  text-transform: capitalize;
  margin: 0 0 2px;
}

.box .content .text-holder p {
  color: #fdc900;
  font-size: 14px;
  font-weight: 300;
  font-style: italic;
  margin: 0;
}

.single-project-item:hover .img-holder .overlay-style-one {
  opacity: 1;
  transform: perspective(400px) rotateX(0deg);
}

.img-holder {
  display: block;
  overflow: hidden;
  position: relative;
}

.spacer-50 {
  height: 50px;
  width: 100%;
}

.show .filter-item {
  height: 240px;
  width: 250px;
  margin: 10px;
  margin-bottom: 50px;
}

.hidden {
  opacity: 0;
}

.show img {
  visibility: visible;
  opacity: 1;
}
