@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400&family=Roboto&display=swap');


.sectionTitle{
    font-size: 2.25rem;
    font-weight: normal;
    line-height: 1.33em;
    margin: 0.39em 0 0.61em;
    padding-bottom: 0.28em;
    position: relative;
    font-family:'Lato', sans-serif;
}

.sectionTitle::after{
    content: "";
    background-color: #B17B1A;
    width: 80px;
    height: 2px;
    position: absolute;
    left: 0;
    bottom: 0;
}

.sectionTitle span {
    font-size: 1rem;
    color: #1f252e;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-family: 'Lato', sans-serif;
    font-weight:400;
}

.aboutInfo{
font-family: "Roboto", sans-serif;
font-size: 16px;
font-size: 1rem;
-webkit-font-smoothing: antialiased;
overflow-x: hidden;
}

.aboutInfo p{
  color:#1f252e;
  line-height: 1.8em;
}

.img-container{
position: relative;
top: -25px;
}

.img-container img {
    position: absolute;
    -webkit-box-shadow: 0 0 50px 0 #999999;
    -moz-box-shadow: 0 0 50px 0 #999999;
    -o-box-shadow: 0 0 50px 0 #999999;
    -ms-box-shadow: 0 0 50px 0 #999999;
    box-shadow: 0 0 50px 0 #999999;
}

.img-container img:first-child {
    top: 80px;
    left: 220px;
    z-index: 9;
    height:245px;
    width:235px;
}

.img-container img:nth-child(2) {
    top: 200px;
    right: 220px;
    z-index: 10;
    height:337px;
    width:248px;
}

 .img-container img:nth-child(3) {
    top: 210px;
    right: 0;
    height:245px;
   width:235px;
}


@media  (max-width: 600px) {
    .img-container {
        display: none;   
     }
  }

  @media only screen and (min-width: 600px){
      .img-container{
          display: none;
      }
  }

  @media only screen and (max-width: 768px){
      .img-container{
          display:block;
      }
    .img-container img:first-child {
        top:100px;
        right:0;
        left:100px
    }

    .img-container img:nth-child(2){
        top:220px;
        right:50px;
    }



    .img-container img:nth-child(3){
        top:400px;
        right:0;
    }

    .aboutInfo {
        padding:20px;
    }
  }

  @media only screen and (max-width: 992px) {
    .img-container{
        display:block;
    }
  .img-container img:first-child {
      top:80px;
      right:0;
      left:100px
  }

  .img-container img:nth-child(2){
      top:200px;
      left:0px;
  }



  .img-container img:nth-child(3){
      top:210px;
      right:0;
  }
  }

  @media only screen and (max-width: 1200px){
    .img-container{
        display:block;
    }
  .img-container img:first-child {
      top:80px;
      right:0;
      left:100px
  }

  .img-container img:nth-child(2){
      top:200px;
      left:0px;
  }



  .img-container img:nth-child(3){
      top:210px;
      right:0;
  }
  } 

  .img-container{
    display:block;
}